import React from "react";
import "./Tasks.css";
import { TaskComponent } from "./TaskComponent";
import { TaskForm } from "./TaskForm";
import { useTasks, TaskModel, Task, TaskState } from "../../Store/tasks";
import { Button } from "../Utility/Button/Button";
import { PageLayout } from "../App/PageLayout";

type TaskFormState = {
  isOpen: boolean;
  taskId?: string;
};

function getStateOrder(state: TaskState): number {
  switch (state) {
    case "active": return 0;
    case "in-backlog": return 1;
    case "created": return 2;
    case "finished": return 3;
    case "abandoned": return 4;
  }
}

function compare(task1: Task, task2: Task): number {
  if (task1.state !== task2.state) {
    return getStateOrder(task1.state) < getStateOrder(task2.state) ? -1 : 1;
  }

  // both tasks have the same state

  return task1.title.localeCompare(task2.title);
}

function sortTasks(tasks: TaskModel): Task[] {
  return Object.keys(tasks)
    .map(id => tasks[id])
    .sort(compare);
}

export function TaskList() {
  const tasks = useTasks();
  const [taskForm, setTaskForm] = React.useState<TaskFormState>({ isOpen: false });

  /*React.useEffect(() => {
    (async () => {
      try {
        setData(await getData());
      } catch (error) {
        setData(error.toString());
      }
    })();
  }, []);*/

  return <PageLayout>
    <div className="taskList">
      <div className="tasks">
        {sortTasks(tasks).map(task => <TaskComponent key={task.id} task={task} onClick={() => setTaskForm({ isOpen: true, taskId: task.id })} />)}
      </div>
      <div className="tasksButtons">
        <Button isWide onClick={() => setTaskForm({ isOpen: true, taskId: undefined })}>+</Button>
      </div>
      {
        taskForm.isOpen &&
        <TaskForm
          task={taskForm.taskId ? tasks[taskForm.taskId] : undefined}
          onClose={() => setTaskForm({ isOpen: false })}
        />}
    </div>
  </PageLayout>;
}
