import React from 'react';
import './App.css';
import { Link, } from "react-router-dom";
import { useAuth } from '../../Auth/UseAuthHook';
import { AuthStatus } from '../AuthStatus/AuthStatus';
import { Button } from '../Utility/Button/Button';
import { useFetchData, useSaveData } from '../../Store/dataStorage';
import reload from '../../Assets/reload.png';

export const PageLayout: React.FunctionComponent = props => {
  const authState = useAuth();
  const fetchData = useFetchData();
  const saveData = useSaveData();

  return <>
    <header>
      <Link to="/home">Home</Link>
      <Link to="/tasks">Tâches</Link>
      <AuthStatus authState={authState} />
      <Button onClick={fetchData} isLight={true} title="Recharger les données"><img src={reload} alt="Recharger les données" /></Button>
      <Button onClick={saveData} isLight={true}>Enregistrer</Button>
    </header>
    <section>
      {props.children}
    </section>
  </>
}